.header{
    position: sticky;
    top: 0;
    display: flex;
    justify-content: space-evenly;
    border-bottom: 0.1px solid lightgrey;
    padding-top: 10px;
    padding-bottom: 10px;
    border-top: 10px;
    width: 100%;
    z-index: 999;
    background-color: white;

}
.header__left{
    display: flex;
}
.header__left > img{
    object-fit: contain;
    height: 40px;
    margin-right: 10px;
}

.header__search{
    padding: 10px;
    display: flex;
    align-items: center;
    border-radius: 5px;
    height: 22px;
    color: grey;
    background-color: #eef3f8;

}

.header__search>input{
    outline: none;
    border: none;
    background: none;
}

.header__right{
    display: flex;
}