.widgets{
    position: sticky;
    top: 80px;
    flex: 0.2;
    background-color: #fff;
    border-radius: 10px;
    border: 1px solid lightgrey;
    height: fit-content;
    padding-bottom: 10px;
}
.widgets__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
}
.widgets__header > h2{
    font-size: 16px;
    font-weight: 600;
}
